ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #333;
}

li {
  float: left;
  justify-content: space-between;
}

li a {
  display: flex;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  align-items: center;

  &.active {
    color: #111;
  }
}

li a:hover {
  background-color: grey;
}

.nav-right {
  float: right;
}

.logo-image {
  width: 100%;
  max-width: 30px;
}
